<template>

<v-card>
        <v-card-title><v-icon>search</v-icon>Search
            <v-spacer></v-spacer>
                <v-text-field v-model="search" label="제목 & 작성자" single-line
                    hide-details></v-text-field>
            </v-card-title>  
                    <v-data-table hide-actions 
                       :headers="headers"
                        :items="items"
                        :search="search"
                        :pagination.sync="pagination">
                       
                        <template slot="items" slot-scope="props">
                            <td :class="headers[0].class">{{props.item.title}}</td>
                            <td :class="headers[1].class">{{props.item.writer}}</td>
                            <td :class="headers[2].class">{{props.item.createdAt}}</td>
                        </template>
                        <v-alert slot="no-results" :value="true" color="error" icon="warning">
                             "{{search}}" no result.
                        </v-alert>
                   </v-data-table> 

                    <div v-for="item in items" :key="item.id" :pagination.sync="pagination"> 
                        <v-card-text>{{item.title}}</v-card-text>
                          <v-pagination v-model="pagination.page" :length="pages"></v-pagination>
                    </div>
                </v-card-text>
</v-card>
</template>

<script>
export default {
    name:'EvaluationBoard',

    data: function(){
        return {
            search: '',
            info:'',
            items:[],
            pagination:{
                page:1,
                total:5,
                visible:7
            },
            headers: [
                {text:"제목", value:"title" ,sortable:false},
                {text:"작성자", value:"writer", sortable:false},
                {text:"작성일", value:"createdAt",sortable:false}
            ],    
        methods:{
            // get : function(){
                // this.axios.get('/board/list').then(({response})=>{
            // if (!response.success) throw new Error(response.msg)
            // for(var i=0; i<response.data.length; i++){
                // items[i].title=response.data[i].title
                // items[i].writer=response.data[i].writer
            //     // items[i].createdAt=response.data[i].createdAt
        //       }).catch((err)=>{
        //           this.pop(e.message,'error')
        //       })
        //   }
        // }, 
                // alert('hi')  
                // this.$http.get('http://localhost:3000/board/list').then((res)=>{
                //     this.items=res.data
                //     console.log(items)
                // }).catch((err)=>{
                //     console.log(err)})
            },

      mounted(){
    
            console.log('hi')
      },
       computed: {
          pages () {
            if (this.pagination.rowsPerPage == null ||
               this.pagination.totalItems == null   ) return 0

               }
          }
        }
    }
}


</script>

